#header {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.2)), to(rgba(0, 0, 0, 0.2))), url("../assets/bg.jpg") no-repeat center;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../assets/bg.jpg") no-repeat center;
  background: -o-linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../assets/bg.jpg") no-repeat center;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url("../assets/bg.jpg") no-repeat center;
  background-size: cover;
  height: 100vh; }
  @media only screen and (min-width: 768px) {
    #header {
      background-attachment: fixed; } }
  #header a.scroll {
    position: absolute;
    bottom: 1rem;
    left: 50%;
    z-index: 2;
    display: inline-block;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-transition: opacity .3s;
    -o-transition: opacity .3s;
    transition: opacity .3s;
    font-size: 1.5rem;
    color: #fff;
    font-weight: bold; }
    #header a.scroll:hover {
      opacity: .5;
      cursor: pointer; }
  #header span.scroll-icon {
    position: absolute;
    left: 50%;
    bottom: 2rem;
    width: 30px;
    height: 50px;
    margin-left: -15px;
    border: 2px solid #fff;
    border-radius: 50px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
    #header span.scroll-icon::before {
      position: absolute;
      top: 10px;
      left: 50%;
      content: '';
      width: 6px;
      height: 6px;
      margin-left: -3px;
      background-color: #fff;
      border-radius: 100%;
      -webkit-animation: scrollDown 2s infinite;
              animation: scrollDown 2s infinite;
      -webkit-box-sizing: border-box;
              box-sizing: border-box; }
  #header button.scroll-to-top-icon {
    display: block;
    position: fixed;
    right: 2rem;
    bottom: 2rem;
    background-color: #000;
    color: #fff;
    opacity: 1;
    -webkit-transition: opacity 1s ease, -webkit-transform .3s ease;
    transition: opacity 1s ease, -webkit-transform .3s ease;
    -o-transition: opacity 1s ease, transform .3s ease;
    transition: opacity 1s ease, transform .3s ease;
    transition: opacity 1s ease, transform .3s ease, -webkit-transform .3s ease;
    z-index: 99; }
    #header button.scroll-to-top-icon:hover {
      background-color: #4aacf1;
      -webkit-transform: rotate(360deg) scale(1.2);
          -ms-transform: rotate(360deg) scale(1.2);
              transform: rotate(360deg) scale(1.2); }
    #header button.scroll-to-top-icon.hide {
      opacity: 0; }

@-webkit-keyframes scrollDown {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  40% {
    opacity: 1; }
  80% {
    opacity: 0;
    -webkit-transform: translate(0, 20px);
            transform: translate(0, 20px); }
  100% {
    opacity: 0; } }

@keyframes scrollDown {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  40% {
    opacity: 1; }
  80% {
    opacity: 0;
    -webkit-transform: translate(0, 20px);
            transform: translate(0, 20px); }
  100% {
    opacity: 0; } }



